function Timeline() {
    const [index, setIndex] = useState(3);

    const loadMore = () => {
        let newIndex = index + 4;
        if (newIndex > timeline.length)
            newIndex = timeline.length;
        setIndex(newIndex)
    }

    const addButton = () => {
        if (index < timeline.length)
            return (
                <AddIcon />);
        else return (
            <DotIcon />
        );
    };

    const getTimelineElements = () =>
        timeline.slice(0, index).map((step, ind) => (
            <VerticalTimelineElement
                key={ind}
                position={step.type === "work" ? "left" : "right"}
                triggerOnce={false}
                contentStyle={{ background: "#17b978", color: "#fff" }}
                contentArrowStyle={{ borderLeft: "7px solid  #086972" }}
                date={step.date}
                dateClassName="education-date"
                iconStyle={{ background: "#071a52", color: "#fff" }}
                icon={step.icon}
            >
                <h3 dangerouslySetInnerHTML={{ __html: step.event }} />
                <h5 dangerouslySetInnerHTML={{ __html: step.detail }} />
                <span><GiPositionMarker />{step.location}</span>
            </VerticalTimelineElement>
        ));
    return (
        <section className="section timeline" id="timeline">
            <div>
                <h1>Timeline</h1>
                <VerticalTimeline >
                    {getTimelineElements()}
                    <VerticalTimelineElement
                        iconOnClick={loadMore}
                        iconStyle={{
                            background: "#17b978",
                            color: "#071a52",
                            cursor: "pointer"
                        }}
                        icon={addButton()}
                    />
                </VerticalTimeline>
            </div>
        </section>
    );
}

export default Timeline;
